import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { UserProfileComponent } from './user-profile.component';
import { MatIconModule } from '@angular/material/icon';
import { IconsModule } from 'src/app/icons.module';
import { Route, RouterModule } from '@angular/router';
import { WidgetsModule } from 'src/app/widgets/chq-widgets.module';
import { VersionControlResolver } from 'src/app/resolvers/versionControl.resolver';
import { TranslateModule } from '@ngx-translate/core';

const routes: Route[] = [
  { 
    path: '', 
    component: UserProfileComponent,
    resolve: { version: VersionControlResolver }
  }
];

@NgModule({
  declarations: [
    UserProfileComponent
  ],
  imports: [
    CommonModule,
    MatIconModule,
    IconsModule,
    RouterModule.forChild(routes),
    WidgetsModule,
    TranslateModule
  ]
})
export class UserProfileModule { }
