import { Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { EditMobileDialogComponent } from 'src/app/dialogs/edit-mobile-dialog/edit-mobile-dialog.component';
import { ChqWidgetsButtonModel } from 'src/app/model/chq-widgets-button-model';
import { AuthService } from 'src/app/services/auth/auth.service';
import { CommonService } from 'src/app/services/common/common.service';
import { CoreService } from 'src/app/services/core/core.service.service';
import { MonitorService } from 'src/app/services/monitor-service/monitor.service';

export interface Organization {
  name: string;
  cityName: string;
  countryName: string;
}

export interface Manager {
  firstName: string;
  lastName: string;
  userProfileImageBlobUrl?: string;
  avatarText?: string;
}

export interface Role {
  description: string;
  automotiveServiceName: string;
  name: string;
}

export interface User {
  userProfileImageBlobUrl?: string;
  firstName: string;
  lastName: string;
  countryCode: string;
  phoneNumber: string;
  email: string;
  function: string;
  organizationDetail: Organization;
  managerDetail: Manager;
  roleDetail: Role[];
}

@Component({
  selector: 'user-profile',
  templateUrl: './user-profile.component.html',
  styleUrls: [ './user-profile.component.scss' ]
})
export class UserProfileComponent implements OnInit{
  public resetButtonModel: ChqWidgetsButtonModel = {
    label: '',
    type: 'primary'
  }
  public editMobileNumber: ChqWidgetsButtonModel = {
    label: '',
    type: 'outline'
  }
  @ViewChild('uploader') uploader: ElementRef;
  avatarText: string;
  userDetail: User;
  hasAvatar:boolean=true;
  /**
   * constructor
   * @param commonService
   */
  constructor(
    private commonService: CommonService,
    private coreService: CoreService,
    private authService: AuthService,
    private monitorService: MonitorService,
    private dialog: MatDialog
  ) {
    this.commonService.reloadUser.subscribe((reload) => {
      if (reload) {
        this.getProfile();
      }
    });
  }
  /**
   * on init
   */
  ngOnInit(): void {
    this.monitorService.logEvent('ngOnInit', [ 'ChqUserProfileComponent', 'addenda-quote' ]);

    this.resetButtonModel = {
      label: 'Reset Password',
      type: 'primary',
      icon: 'refresh',
      onclick: this.resetPassword.bind(this)
    }
    this.editMobileNumber = {
      label: 'Edit mobile number',
      type: 'outline',
      icon: 'edit',
      onclick: this.changePhoneNumber.bind(this)
    }
    if (this.commonService.userProfileLoading) {
      this.commonService.showLoading();
      this.commonService.userProfileData.subscribe({
        next: (res: any) => {
          if (res) {
            this.handleUserProfileData(res);
            this.commonService.hideLoading();
          }
          
        },
        error: () => {
          this.getProfile();
        }
      });
    } else {
      this.getProfile();
    }
  
  }

  /**
   * handle user profile data
   */
  async handleUserProfileData(res:any):Promise<void> {
    if (res?.data.userProfileImageBlobUrl) {
      this.commonService.avatar.next({
        image: res?.data.userProfileImageBlobUrl
      });
    }           
    this.userDetail = res.data;
    if (!this.userDetail.phoneNumber) {
      this.editMobileNumber.type = 'disabled-outline';
    }
    if (this.userDetail?.managerDetail) {
      const avatarText = `${this.userDetail?.managerDetail?.firstName.charAt(0)}${this.userDetail?.managerDetail?.lastName.charAt(0)}`;
      this.userDetail.managerDetail.avatarText = avatarText;
    }
    this.avatarText = `${this.userDetail?.firstName.charAt(0)}${this.userDetail?.lastName.charAt(0)}`;
  }

  /**
   * open change phonenumber dialog
   */
  changePhoneNumber():void {
    this.monitorService.logEvent('changePhoneNumber', [ 'ChqUserProfileComponent', 'addenda-quote' ]);

    const dialogRef = this.dialog.open(EditMobileDialogComponent, {
      data:
        this.userDetail,
      autoFocus: false 
    });

    dialogRef.afterClosed().subscribe();
  }

  /**
   * get user profile info
   */
  getProfile(): void {
    this.commonService.showLoading();
    this.monitorService.logEvent('getProfile', [ 'ChqUserProfileComponent', 'addenda-quote' ]);
    this.coreService.getUserProfile().subscribe({
      next: async (res) => {
        if (res?.data.userProfileImageBlobUrl) {
          this.commonService.avatar.next({
            image: res?.data.userProfileImageBlobUrl
          });
        }           
        this.userDetail = res.data;
        if (!this.userDetail.phoneNumber) {
          this.editMobileNumber.type = 'disabled-outline';
        }
        if (this.userDetail?.managerDetail) {
          const avatarText = `${this.userDetail?.managerDetail?.firstName.charAt(0)}${this.userDetail?.managerDetail?.lastName.charAt(0)}`;
          this.userDetail.managerDetail.avatarText = avatarText;
        }
        this.avatarText = `${this.userDetail?.firstName.charAt(0)}${this.userDetail?.lastName.charAt(0)}`;
        this.commonService.hideLoading();        
        this.commonService.userProfileData.next(res);
      },
      error: () => {
        this.commonService.hideLoading();
      }
    })
  }
  

  /** 
   * open labour rate popup
   */
  openOtpPopup(): void {
    this.monitorService.logEvent('openOtpPopup', [ 'ChqUserProfileComponent', 'addenda-quote' ]);

    const dialogRef = this.dialog.open(EditMobileDialogComponent, {
      data:
        { 'phoneNumber': '', 'isPhoneNumberAddition': true },
      autoFocus: false 
    });

    dialogRef.afterClosed().subscribe();
  }

  /**
   * handles reset button click
   * returns void
   */
  public resetPassword(): void {
    this.monitorService.logEvent('resetPassword', [ 'ChqUserProfileComponent', 'addenda-quote' ]);
    window.location.href = this.authService.resetPasswordRoute();
  }

  /**
   * handle file upload
   * @param event 
   */
  handleUpload(event: any): void {
    const currentFile = event.target.files || event.srcElement.files;
    this.monitorService.logEvent('handleUpload', [ 'ChqUserProfileComponent', 'addenda-quote', {
      event
    } ]);
    if (currentFile !== null && currentFile !== '' && currentFile.length > 0) {
      if (!this.checkFileSize(currentFile[0])) {
        const message = 'Image size is above the allowed 5MB limit, try again with a smaller image';
        this.commonService.showToast(0, message);
        this.uploader.nativeElement.value = null;
        return;
      }
      if (!this.isValidImageFile(currentFile[0].name)) {
        const message = 'Uploaded file type is not supported';
        this.commonService.showToast(0, message);
        this.uploader.nativeElement.value = null;
        return;
      }
      const reader = new FileReader();
      reader.readAsDataURL(currentFile[0]);
      reader.onload = (_event: any): void => {
        this.uploadImage(reader.result, currentFile[0].name, currentFile[0]);
      }
    } else {
      this.uploader.nativeElement.value = null;
      return;
    }
  }

  /**
   * checks file size
   * @param blob 
   * @returns boolean
   */
  checkFileSize(img: Blob): boolean {
    this.monitorService.logEvent('checkFileSize', [ 'ChqUserProfileComponent', 'addenda-quote', {
      fileSize: img.size
    } ]);
    if (img.size > this.commonService.maxileSize) {
      return false;
    }
    return true;
  }

  /**
   * checkx filename
   * @param filename 
   * @returns 
   */
  public isValidImageFile(filename: string): boolean {
    const regex: RegExp = new RegExp('^.*\\.[a-zA-Z]+$', 'gm');
    this.monitorService.logEvent('isValidImageFile', [ 'ChqUserProfileComponent', 'addenda-quote', {
      fileName: filename
    } ]);
    if (regex.test(filename)) {
      const extension = filename.split('.').pop();
      return this.commonService.allowExtension.includes(extension?.toLowerCase());
    }
    return false;

  }

  /**
   * upload image to server
   * @param image 
   * @param fileName 
   * @param file 
   */
  uploadImage(image: string | ArrayBuffer, fileName: string, file: File): void {
    this.monitorService.logEvent('uploadImage', [ 'ChqNewPhotosComponent', 'addenda-quote', {
      fileName: fileName
    } ]);
    this.commonService.showLoading();
    this.coreService.uploadProfileImage(file).subscribe({
      next: async (res) => {
        if (res) {         
          this.userDetail.userProfileImageBlobUrl = res?.data.userImageBlobUrl;
          this.hasAvatar = true;
          this.commonService.avatar.next({
            image: res?.data.userImageBlobUrl
          });
        }
        this.uploader.nativeElement.value = null;
        this.commonService.hideLoading();
      },
      error: () => {
        this.uploader.nativeElement.value = null;
        this.commonService.hideLoading();
      }
    })
  }

  /**
   * delete dialog
   */
  delete(): void {
    this.monitorService.logEvent('delete', [ 'ChqNewPhotosComponent', 'addenda-quote' ]);
    this.commonService.openDeleteDialog()
      .afterClosed().subscribe((data) => {
        if (data) {
          this.deleteProfilePic();
        } 
      });
  }

  /**
 * delete profile picutre
 */
  deleteProfilePic(): void {
    this.monitorService.logEvent('deleteProfilePic', [ 'ChqNewPhotosComponent', 'addenda-quote' ]);
    this.commonService.showLoading();
    this.coreService.deleteUserProfile().subscribe({
      next: async (res) => {
        this.userDetail.userProfileImageBlobUrl = null;
        this.commonService.avatar.next({
          image: null,
          avatar: this.avatarText
        });
        this.commonService.hideLoading();
      },
      error: () => {
        this.commonService.hideLoading();
      }
    })
  }
}


