import { Injectable } from '@angular/core';
import { Resolve } from '@angular/router';

import { catchError, map, Observable, of } from 'rxjs';
import { CommonService } from '../services/common/common.service';

@Injectable({
  providedIn: 'root'
})
export class VersionControlResolver implements Resolve<Observable<any>> {
  /**
   * contructor
   * @param commonService 
   */
  constructor(private commonService: CommonService) { }
  /**
   * resolve the 
   * @returns 
   */
  resolve(): Observable<any> {
    this.commonService.showLoading();
    return this.commonService.checkVersion().pipe(
      map((res) => {
        this.commonService.hideLoading();
        const localVersion = localStorage.getItem('appVersion');
        const appVersion = res.buildRevision;
        if (localVersion && localVersion != appVersion) {
          localStorage.setItem('appVersion', appVersion);
          setTimeout(() => {
            location.reload();
          }, 10);
          return of('new version available');
        } else {
          localStorage.setItem('appVersion', appVersion);
          return of('new version is not available');
        }
      }),
      catchError(() => {
        this.commonService.hideLoading();
        return of('data not available at this time');
      })
    )
  }
}